<template>
    <div class="pagination" >
      <!-- 上一页按钮 -->
      <button
        class="pagination-button"
        :disabled="currentPage <= 1"
        @click="goToPreviousTenPages"
      >
        <van-icon name="arrow-left" />
      </button>
  
      <!-- 页码显示 -->
      <div class="page-numbers" :key="componentKey">
        <span 
          v-for="page in visiblePages"
          :key="page"
          :class="{ active: activePages[page] }"
          @click="goToPage(page)"
        >
          {{ page }}
        </span>
      </div>
  
      <!-- 下一页按钮 -->
      <button
        class="pagination-button"
        :disabled="currentPage >= totalPages"
        @click="goToNextTenPages"
      >
        <van-icon name="arrow" />
      </button>
    </div>
  </template>
  
  <script>
  export default {
    name: "Pagination",
    props: {
      totalPages: {
        type: Number,
        required: true,
      },
      currentPage: {
        type: Number,
        required: true,
      },
    },
    data() {
      return {
        componentKey: 0,
      };
    },
    emits: ["page-change"],
    watch: {
      // 监听 currentPage 的变化
      // currentPage(newVal) {
      //   console.log('curennn,',newVal)
      //   this.updateActivePages(newVal);
      // },
      // 监听 visiblePages 的变化
      visiblePages: {
        handler(newVal) {
          this.updateActivePages(this.currentPage);
        },
        immediate: true,
      },
    },
    computed: {
  // 计算当前显示的页码范围 (1-5, 6-10, 11-15 ...)
  visiblePages() {
      const startPage = Math.floor((this.currentPage - 1) / 5) * 5 + 1;
      const endPage = Math.min(startPage + 4, this.totalPages);
      return Array.from({ length: endPage - startPage + 1 }, (_, i) => startPage + i);
    },
  },
  methods: {
    // 更新 activePages 的状态
    updateActivePages(currentPage) {
      this.activePages = {};
      this.visiblePages.forEach((page) => {
        this.activePages[page] = page === currentPage;
      });
    },

    // 跳转到指定页码
    goToPage(page) {
      if (page >= 1 && page <= this.totalPages) {
        this.$emit("page-change", page);
      }
    },

    // 跳转到上一组 5 页
    goToPreviousTenPages() {
      const newPage = Math.max(1, this.currentPage - 5);
      this.$emit("page-change", newPage);
    },

    // 跳转到下一组 5 页
    goToNextTenPages() {
      const newPage = Math.min(this.totalPages, this.currentPage + 5);
      // console.log(newPage,"son next=?")
      this.$emit("page-change", newPage);
    }
  }

  };
  </script>
  
  <style scoped>
  .pagination {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 10px;
    padding: 10px;
    width: 100%;
  }
  
  .pagination-button {
    padding: 10px 20px;
    border: 1px solid #ccc;
    background-color: #f9f9f9;
    cursor: pointer;
    border-radius: 4px;
    transition: background-color 0.3s;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  
  .pagination-button:disabled {
    background-color: #e0e0e0;
    cursor: not-allowed;
  }
  
  .pagination-button:hover:not(:disabled) {
    background-color: #ddd;
  }
  
  .page-numbers {
    display: flex;
    gap: 15px;
  }
  
  .page-numbers span {
    padding: 10px 12px;
    border: 1px solid #ccc;
    cursor: pointer;
    color: white;
    border-radius: 4px;
    transition: background-color 0.3s;
  }
  
  .page-numbers span.active {
    background-color: #007bff;
    color: red;
    border-color: #007bff;
  }
  
  .page-numbers span:hover:not(.active) {
    background-color: #f1f1f1;
  }
  
  @media (max-width: 750px) {
    .pagination {
        display: flex;
        justify-content: center;
        align-items: center;
        gap: 15px;
        padding: 1px;
        width: 100%;
        margin-bottom: 12px;
    }
    .pagination-button {
      padding: 5px 8px;
      border-radius: 3px;
    }
    .page-numbers {
      /* flex-wrap: wrap; */
      justify-content: center;
      padding: 10px 12px;
      gap: 12px;
      font-size: medium;
    }
    .page-numbers span {
      padding: 5px 8px; 
    }
  }
  </style>
  